<div class="board"><div class="board-row" v-for="row,i in board" :key="i"><PipeCell v-for="cell,j in row" :key="j" ref="cell" :type="cell.type" v-model:rotation="cell.rotation" v-model:isConnected="cell.isConnected" v-model:isLocked="cell.isLocked" v-model:isSource="cell.isSource" @cell-rotated="checkBoard()"></PipeCell></div></div><div class="data" style="margin-top: 40px; display: flex; justify-content: center;"><pre style="text-align: left; font-size: 18px;">{{ JSON.stringify(board, null, 2) }}</pre></div>